@import '~antd/dist/antd.css';
.container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #8d8d8d;
}
#drum-machine {
    padding: 2rem;
    padding-top: 0;
    background-color: #b3b3b3;
    flex-basis: 50%;
    border: 0.5rem solid #ffa500;
}
header {
    font-size: large;
    font-weight: 900;
    font-style: italic;
    text-align: end;
    position: relative;
    left: 2rem;
    margin-bottom: 1rem;
}
header i {
    text-decoration: underline;
}
#drum-pad {
    width: 50%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 0.5rem;
}
#drum-pad div {
    font-size: large;
    border-radius: 5%;
    text-align: center;
    box-shadow: 2px 2px 5px #000000;
    vertical-align: middle;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.1s;
    background-color: #808080;
    padding: 1.5rem;
}
.drum-content {
    display: flex;
}
.controls {
    width: 50%;
}
.controls h2 {
    text-align: center;
}
.controls button {
    display: block;
    margin: auto;
    margin-bottom: 1rem;
}
.controls #display {
    width: 80%;
    padding: 1.5rem;
    font-size: large;
    font-weight: bold;
    background-color: #8d8d8d;
    margin: auto;
}
.controls .slider {
    width: 80%;
    margin: auto;
    margin-top: 1rem;
}
.active {
    background-color: #ffa500 !important;
    box-shadow: none !important;
}
